.noContact {
  text-align: center;
  font-size: 20px;
  color: #777777;
  margin-top: 10px;
}
.contactList {
  display: flex;
  flex-direction: column;
  gap: 15px;
  height: 400px;
  overflow-y: auto;
}
.contactList::-webkit-scrollbar {
  width: 8px;
}
.contactList::-webkit-scrollbar-track {
  background: transparent;
}
.contactList::-webkit-scrollbar-thumb {
  background-color: rgba(200, 134, 255, 0.66);
  border-radius: 20px;
}

.contactItem {
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
}

.imgNameWrapp {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 300px;
}

.image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.descrList {
  display: flex;
  gap: 20px;
}

.descrEmailItem {
  font-size: 15px;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 3px;
  width: 300px;
}

.descrPhoneItem {
  font-size: 15px;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 3px;
  width: 170px;
}

.desription {
  font-size: 20px;
  color: white;
}

.iconList {
  display: flex;
  gap: 15px;
}

.iconItem {
  display: flex;
  align-items: center;
}

.deleteBtn {
  display: flex;
  align-items: center;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

.deleteIcon {
  fill: white;
  width: 35px;
  height: 35px;
  transition: all 250ms linear;
}

.deleteBtn:hover .deleteIcon,
.deleteBtn:focus .deleteIcon {
  fill: red;
}

.favoriteBtn {
  display: flex;
  align-items: center;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

.starIcon {
  fill: #f0ff1a;
  width: 35px;
  height: 35px;
  transition: all 300ms linear;
}

.editBtn {
  display: flex;
  align-items: center;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

.editIcon {
  fill: white;
  width: 28px;
  height: 28px;
  transition: all 300ms linear;
}

.editBtn:hover .editIcon,
.editBtn:focus .editIcon {
  fill: greenyellow;
}

